.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 1rem 5rem;
}

.navbar > a {
  text-decoration: none;
}

.navbar > ul {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 3rem;
}

.navbar li {
  list-style: none;
  margin-right: 2rem;
}

.navbar li a {
  text-decoration: none;
}
