.ground {
  min-height: 100vh;
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
}

footer {
  background: #faf5f5;
  padding: 20px 0;
}

.footer_container {
  max-width: 1300px;
  margin: auto;
  padding: 0 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap-reverse;
}

.logo {
  width: 180px;
  font-size: 16px;
  font-weight: bold;
}

.social_media {
  margin: 20px 150px;
}

.social_media a {
  color: #000;
  font-size: 24px;
  text-decoration: none;
  transition: 0.6s;
  margin: 0 2rem;
}

.social_media a:hover {
  color: gold;
  transform: translate(1.5);
}
